import axios from 'axios'
import { AxiosInstance } from 'axios'
import { ElMessage } from 'element-plus'
import { DFRequestConfig, DFInterceptors, ValidResponse } from './types'

/**
 * 请求拦截器先后顺序：局部拦截器、全局私有拦截器、全局公有拦截器、
 * 响应拦截器先后顺序：全局公有拦截器、全局私有拦截器、局部拦截器
 */

export default class DFAxios {
  _instance: AxiosInstance
  interceptors?: DFInterceptors<ValidResponse, ValidResponse>

  constructor(config: DFRequestConfig<ValidResponse>) {
    // 创建axios的实例
    this._instance = axios.create(config)
    // 保存全局私有拦截器
    this.interceptors = config.interceptors
    // 设置全局公有请求拦截器
    this._instance.interceptors.request.use(
      (config) => {
        return config
      },
      (err) => {
        return err
      }
    )
    // 设置全局私有请求拦截器
    this._instance.interceptors.request.use(
      this.interceptors?.requestInterceptor,
      this.interceptors?.requestInterceptorCatch
    )
    // 设置全局公有响应拦截器
    this._instance.interceptors.response.use<ValidResponse>(
      (res) => {
        if (res.status == 200) {
          return res.data
        } else {
          throw res.statusText
        }
      },
      (err) => {
        ElMessage({
          message: err.message,
          type: 'error'
        })
        // return err // 不返回错误的话，不会继续抛出错误
      }
    )
  }

  /**
   * 手动执行全局私有拦截器，不用axios库中的use方法
   * @param T 全局私有拦截器的返回值类型
   */
  _runPrivateInterceptor<T, F>(
    config: DFRequestConfig<T, F>
  ): Promise<ValidResponse> {
    const cancel = config.cancelGlobalInterceptor ?? false
    return new Promise((resolve, reject) => {
      // 调用axios的request发送请求
      this._instance
        .request<any, ValidResponse>(config)
        .then((res) => {
          if (this.interceptors?.responseInterceptor && !cancel) {
            // 执行全局私有拦截器
            resolve(this.interceptors.responseInterceptor(res))
          } else {
            resolve(res)
          }
        })
        .catch((err) => {
          reject(err)
          return err
        })
    })
  }

  /**
   * @param T 局部拦截器返回的数据的类型
   * @param F 局部拦截器得到的数据的类型
   */
  request<F = ValidResponse, T = ValidResponse>(
    config: DFRequestConfig<T, F>
  ): Promise<T> {
    return new Promise((resolve, reject) => {
      // 执行局部请求拦截器
      if (config.interceptors?.requestInterceptor) {
        config = config.interceptors.requestInterceptor(config)
      }
      // 调用自己的request发送请求, 为了手动执行全局私有拦截器
      const a = this._runPrivateInterceptor<T, F>(
        config
      ) as unknown as Promise<F>
      a.then((res) => {
        // 执行局部响应拦截器
        if (config.interceptors?.responseInterceptor) {
          // resolve返回出去
          resolve(config.interceptors.responseInterceptor(res))
        } else {
          resolve(res as unknown as T)
        }
      }).catch((err) => {
        reject(err)
        return err
      })
    })
  }

  /**
   * @param T 局部拦截器返回的数据的类型
   * @param F 局部拦截器得到的数据的类型
   */
  get<F = ValidResponse, T = any>(config: DFRequestConfig<T, F>) {
    return this.request<F, T>({ ...config, method: 'GET' })
  }

  /**
   * @param T 局部拦截器返回的数据的类型
   * @param F 局部拦截器得到的数据的类型
   */
  post<F = ValidResponse, T = ValidResponse>(config: DFRequestConfig<T, F>) {
    return this.request<F, T>({ ...config, method: 'POST' })
  }

  delete<F = ValidResponse, T = any>(config: DFRequestConfig<T, F>) {
    return this.request<F, T>({ ...config, method: 'DELETE' })
  }

  patch<F = ValidResponse, T = any>(config: DFRequestConfig<T, F>) {
    return this.request<F, T>({ ...config, method: 'PATCH' })
  }
}
